<template>
  <div class="block">
    <el-date-picker
      v-model="pickerTime"
      type="week"
      format="yyyy 第 WW 周"
      placeholder="选择周"
      :picker-options="pickerOptions">
    </el-date-picker>
  </div>
</template>

<script>
    export default {
        name: "WeekTimePicker",
      data() {
        return {
          pickerTime: '',
          pickerOptions: {
            disabledDate(time) {
              return time.getTime() > Date.now();
            }
          },
        };
      }
    }
</script>

<style scoped>

</style>



